.container {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  min-width: 340px;
}

.logoContain {
  text-align: center;
}

.escapeHatches {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 64px;
}

.escapeHatches_item {
  font-size: 16px;
  text-align: center;
  color: #008aff;
  text-decoration: none;
}

#formToRegister {
  text-align: center;
}

.lang {
  margin-top: 60px;
  cursor: pointer;
  text-align: center;
  font-weight: 400;
  font-size: 24px;
  color: #7362bc
}

.lang span {
  font-size: 16px;
  line-height: 1.35;
  color: #79747f;
}