
.container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding-left: 50px;
  display: grid;
  grid-template-areas:
    "sidebar header"
    "sidebar head"
    "sidebar calendar"
    "sidebar calendar";
  grid-template-columns: 236px 1fr;
  grid-template-rows: 68px 68px 1fr 1fr;
  background-color: #fff;
}